import React from 'react';

const Card = ({ name, email, id }) => {
  return (
    <div className='tc grow bg-light-green br3 pa3 ma2 dib bw2 shadow-5'>
      <img alt='robots' src={`https://robohash.org/${id || 1}?size=200x200`} />
      <div>
        <h2>{name || 'John Doe'}</h2>
        <p>{email || 'Test@gmail.com'}</p>
      </div>
    </div>
  );
}

export default Card;
